import React, { useState, useEffect } from "react";
import Button from "@mui/material/Button";
import { Link } from "react-router-dom";
import { useCheats } from "hooks/useCheats";
import useUser from "hooks/useUser";
import "./cheat_details_box.css";
import swal from "sweetalert";
import Cheat_Modal from "components/cheats/cheat_modal/cheat_modal";
import Cheat_List from "../cheat_list/cheat_list";
import Lightbox from "react-awesome-lightbox";
import FsLightbox from "fslightbox-react";
import "react-awesome-lightbox/build/style.css";
import TroubleShoot from "components/cheats/troubleshoot/troubleshoot";
import Download_Dialog from "./download_dialog";

//Images
import default_mini from "assets/images/default_mini.png";
import csgo_icon from "assets/iconos/csgo.png";
import gtav_icon from "assets/iconos/gtav.png";
import injectors_icon from "assets/iconos/injector.png";
import others_icon from "assets/iconos/others.png";

const Cheat_Details_Box = ({ cheat }) => {
  const [date, setDate] = useState("");
  const [icon, setIcon] = useState("");
  const {
    deleteCheat,
    setEditingCheat,
    loading,
    setLoading,
    cheats,
    loadingCheats,
  } = useCheats(false);
  const { profile, loadingUser } = useUser(false);
  const [openModal, setOpenModal] = useState(false);
  const [zoomImage, setZoomImage] = useState(false);
  const [liked, setLiked] = useState(false);
  const [toggler, setToggler] = useState(false);

  const imageDefault = (e) => {
    e.target.src = default_mini;
  };

  useEffect(() => {
    select_icon();
    formatDate();
  }, [cheat]);

  useEffect(() => {
    setZoomImage(false);
  }, [toggler == false]);

  function formatDate() {
    if (cheat.updateDate) {
      var t = cheat.updateDate.toDate(); // Epoch
      setDate(t.getDate() + "-" + t.getMonth() + "-" + t.getFullYear());
    }
    // setDate('')
  }

  const select_icon = () => {
    switch (cheat.category) {
      case "csgo":
        setIcon(csgo_icon);
        break;
      case "gtav":
        setIcon(gtav_icon);
        break;
      case "injectors":
        setIcon(injectors_icon);
        break;
      case "others":
        setIcon(others_icon);
        break;
      default:
        break;
    }
  };

  const deleteCheatFunction = () => {
    swal({
      title: "Are you sure?",
      text: "Once deleted, you will not be able to recover this file!",
      icon: "warning",
      buttons: true,
      dangerMode: true,
    }).then((willDelete) => {
      if (willDelete) {
        deleteCheat(cheat.id);
        swal("Poof! The cheat has been successfully removed!", {
          icon: "success",
        });
      } else {
        swal("The cheat is safe!");
      }
    });
  };

  const editCheatFunction = () => {
    setEditingCheat(cheat);
    setOpenModal(true);
  };

  const openVideoUrl = () => {
    if (cheat.videoUrl) {
      window.open(cheat.videoUrl, "_blank").focus();
    } else {
      swal("Coming Soon...", {
        icon: "info",
      });
    }
  };

  return (
    <>
      <div className="cheat_details_page">
        <div className="cheat_details_page_left">
          <div className="cheat_details_box">
            <div className="cheat_details_box_top">
              {/* <div
                className="cheat_details_box_bottom_like"
                onClick={() => {
                  if (liked) {
                    setLiked(false);
                    cheat.likes = cheat.likes - 1;
                  } else {
                    setLiked(true);
                    cheat.likes = cheat.likes + 1;
                  }
                }}
              >
                <div className={liked ? "heart is-liked" : "heart"}></div>
                <span>{cheat.likes}</span>
              </div> */}
              <span className="cheat_details_box_top_fecha">
                <span className="cheat_details_box_top_fecha_icono">
                  <i className="far fa-calendar"></i>
                </span>
                {date}
              </span>
            </div>
            <span className="cheat_details_box_title">
              <img tooltip="csgo" src={icon} />
              <span>{cheat.name}</span>
            </span>
            <div className="cheat_details_box_middle">
              <div className="cheat_details_box_image">
                <img
                  onClick={() => {
                    console.log(cheat.videoUrl ? cheat.videoUrl : "nanai");
                    setZoomImage(!zoomImage);
                  }}
                  src={cheat.imageDetails ? cheat.imageDetails : cheat.imageUrl}
                  alt=""
                  onError={imageDefault}
                />
                <FsLightbox
                  toggler={zoomImage}
                  sources={[
                    cheat.imageUrl ? cheat.imageUrl : null,
                    // cheat.videoUrl ? cheat.videoUrl : null,
                  ]}
                />
              </div>
              <div className="cheat_details_box_description">
                {cheat.description}
              </div>
            </div>
            <div className="cheat_details_box_bottom">
              {/* <span className="cheat_details_box_bottom_like">
                <i className="fas fa-heart is-liked "></i>
                {cheat.likes}
              </span> */}
              <Button
                onClick={openVideoUrl}
                className="cheat_details_box_bottom_videoURL"
                variant="outlined"
                color="error"
              >
                <i className="fab fa-youtube"></i>Tutorial
              </Button>
              <Button
                className="cheat_details_box_bottom_download_button"
                variant="outlined"
                color="success"
                onClick={() => setOpenModal(true)}
              >
                <i className="fas fa-download"></i>Download
              </Button>
            </div>
          </div>
          <div className="cheat_details_page_left_troubleshoot">
            <TroubleShoot />
          </div>
        </div>
        <div className="cheat_details_page_right">
          <div className="other_cheats">
            <span className="other_cheats_title">Other Cheats</span>
            <Cheat_List
              cheatsFiltered={cheats}
              loadingCheatsFiltered={loadingCheats}
              number={3}
            />
          </div>
        </div>
      </div>
      <Download_Dialog open={openModal} setOpen={setOpenModal} cheat={cheat} />
    </>
  );
};

export default Cheat_Details_Box;
