import React, { useState, useEffect } from "react";
import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogTitle from "@mui/material/DialogTitle";
import "./download_dialog.css";
import swal from "sweetalert";
import IconButton from "@mui/material/IconButton";
import CloseIcon from "@mui/icons-material/Close";

//Images
import default_mini from "assets/images/default_mini.png";

export default function Download_Dialog({ open, setOpen, cheat }) {
  var sub1 = {
    url: "https://www.youtube.com/c/Woozy_exe?sub_confirmation=1",
    name: "Woozy",
  };
  var sub2 = {
    url: "https://www.youtube.com/channel/UCx8niOPTLq2jqH5pTr7A5hQ?sub_confirmation=1",
    name: "NemoCheats",
  };
  const [step1State, setStep1State] = useState("open");
  const [step2State, setStep2State] = useState(false);
  const [step3State, setStep3State] = useState(false);
  const [downloadState, setDownloadState] = useState(false);

  useEffect(() => {
    if (sessionStorage.getItem("steps_done")) {
      setStep1State("done");
      setStep2State("done");
      setStep3State("done");
      setDownloadState("open");
    } else {
      setStep1State("open");
      setStep2State(false);
      setStep3State(false);
      setDownloadState(false);
    }
  }, [cheat]);

  const handleClose = () => {
    setOpen(false);
  };

  const imageDefault = (e) => {
    e.target.src = default_mini;
  };

  const clickStep = (number) => {
    switch (number) {
      case 1:
        cheat.videoUrl
          ? window.open(cheat.videUrl, "_blank").focus()
          : swal("Sorry, no video. Wait 3 seconds...", { icon: "info" });
        setTimeout(() => {
          setStep1State("done");
          setStep2State("open");
        }, 3500);
        break;
      case 2:
        if (step1State == "done") {
          window.open(sub1.url, "_blank").focus();
          setTimeout(() => {
            setStep2State("done");
            setStep3State("open");
          }, 3500);
        }
        break;
      case 3:
        if (step2State == "done" && step1State == "done") {
          window.open(sub2.url, "_blank").focus();
          setTimeout(() => {
            sessionStorage.setItem("steps_done", "true");
            setStep3State("done");
            setDownloadState("open");
          }, 3500);
        }
        break;
      default:
        break;
    }
  };

  const downloadClick = (number) => {
    if (step1State == "done" && step2State == "done" && step3State == "done") {
      switch (number) {
        case 1:
          cheat.download1
            ? window.open(cheat.download1, "_blank").focus()
            : swal("Sorry, no download link. Contact Admin...", {
                icon: "error",
              });
          break;
        case 2:
          cheat.download1
            ? window.open(cheat.download2, "_blank").focus()
            : swal("Sorry, no download link. Contact Admin...", {
                icon: "error",
              });
          break;
        default:
          break;
      }
    } else {
      window.location.href = "/";
    }
  };

  return (
    <>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="download_dialog"
        className="download_dialog"
        fullWidth
        maxWidth={"md"}
      >
        <DialogTitle id="download_dialog" className="dialogTitle ">
          <span>{"Download " + cheat.name}</span>
          <IconButton onClick={() => setOpen(false)}>
            <CloseIcon />
          </IconButton>
        </DialogTitle>

        <DialogContent>
          <div className="download_dialog_title">
            <span>
              DO THESE STEPS TO
              <span className="download_dialog_title_unlock"> UNLOCK </span> THE
              DOWNLOAD BUTTON!
            </span>
          </div>
          <div className="stepZone">
            <Button
              color="default"
              disabled={step1State != "open" ? true : false}
              className={
                step1State == "done" ? "stepButton stepDone" : "stepButton"
              }
              variant="outlined"
              onClick={() => clickStep(1)}
            >
              <i className="far fa-thumbs-up"></i>
              <span className="title">LIKE AND COMMENT</span>
            </Button>
            <Button
              color="default"
              disabled={step2State != "open" ? true : false}
              className={
                step2State == "done" ? "stepButton stepDone" : "stepButton"
              }
              variant="outlined"
              onClick={() => clickStep(2)}
            >
              <i className="fas fa-user-alt"></i>
              <span className="title">
                Subscribe to <span className="title_name">{sub1.name}</span>
              </span>
            </Button>
            <Button
              color="default"
              disabled={step3State != "open" ? true : false}
              className={
                step3State == "done" ? "stepButton stepDone" : "stepButton"
              }
              variant="outlined"
              onClick={() => clickStep(3)}
            >
              <i className="fas fa-user-alt"></i>
              <span className="title">
                Subscribe to <span className="title_name">{sub2.name}</span>
              </span>
            </Button>
          </div>
          <div className="downloadZone">
            <Button
              disabled={downloadState != "open" ? true : false}
              className="downloadButton"
              variant="outlined"
              color="default"
              onClick={() => downloadClick(1)}
            >
              <i
                className={
                  downloadState == "open" ? "fas fa-unlock" : "fas fa-lock"
                }
              ></i>
              <span className="title">
                {"DOWNLOAD " + cheat.download1_title}
              </span>
            </Button>
            {cheat.download2 ? (
              <Button
                disabled={downloadState != "open" ? true : false}
                className="downloadButton"
                variant="outlined"
                color="default"
                onClick={() => downloadClick(2)}
              >
                <i
                  className={
                    downloadState == "open" ? "fas fa-unlock" : "fas fa-lock"
                  }
                ></i>
                <span className="title">
                  {"DOWNLOAD " + cheat.download2_title}
                </span>
              </Button>
            ) : null}
          </div>
        </DialogContent>
      </Dialog>
    </>
  );
}
