import React, { useState, useEffect } from "react";
import Button from "@mui/material/Button";
import { Link } from "react-router-dom";
import { useCheats } from "hooks/useCheats";
import useUser from "hooks/useUser";
import "./cheat_box.css";
import swal from "sweetalert";
import Cheat_Modal from "components/cheats/cheat_modal/cheat_modal";
import { FileDownload } from "@mui/icons-material";

//Images
import default_mini from "assets/images/default_mini.png";
import csgo_icon from "assets/iconos/csgo.png";
import gtav_icon from "assets/iconos/gtav.png";
import injectors_icon from "assets/iconos/injector.png";
import others_icon from "assets/iconos/others.png";

const Cheat_Box_Preview = ({ cheat }) => {
  const [date, setDate] = useState(formatDate());
  const [icon, setIcon] = useState("");
  const { deleteCheat, setEditingCheat, loading, setLoading, setCurrentCheat } =
    useCheats(false);
  const { profile, loadingUser } = useUser(false);
  const [openModal, setOpenModal] = useState(false);

  const imageDefault = (e) => {
    e.target.src = default_mini;
  };

  useEffect(() => {
    select_icon();
  }, [cheat]);

  function formatDate() {
    var t = cheat.updateDate.toDate(); // Epoch
    return t.getDate() + "-" + t.getMonth() + "-" + t.getFullYear();
  }

  const select_icon = () => {
    switch (cheat.category) {
      case "csgo":
        setIcon(csgo_icon);
        break;
      case "gtav":
        setIcon(gtav_icon);
        break;
      case "injectors":
        setIcon(injectors_icon);
        break;
      case "others":
        setIcon(others_icon);
        break;
      default:
        break;
    }
  };

  const deleteCheatFunction = (e) => {
    e.stopPropagation();
    swal({
      title: "Are you sure?",
      text: "Once deleted, you will not be able to recover this file!",
      icon: "warning",
      buttons: true,
      dangerMode: true,
    }).then((willDelete) => {
      if (willDelete) {
        deleteCheat(cheat.id);
        swal("Poof! The cheat has been successfully removed!", {
          icon: "success",
        });
      } else {
        swal("The cheat is safe!");
      }
    });
  };

  const editCheatFunction = (e) => {
    e.stopPropagation();
    setEditingCheat(cheat);
    setOpenModal(true);
  };

  const downloadCheat = () => {
    setCurrentCheat(cheat);
    window.location.href = "#/cheat/" + cheat.slug;
  };
  return (
    <>
      <div className="cheat_box" onClick={downloadCheat}>
        <div className="cheat_box_header">
          <img className="icon" tooltip="csgo" src={icon} />
          <img
            className="headerIMG"
            src={cheat.imageUrl}
            alt=""
            onError={imageDefault}
          />
          <div className="cheat_box_details"></div>
        </div>
        <div className="cheat_box_body">
          <span className="fecha">
            <span className="icono">
              <i className="far fa-calendar"></i>
            </span>
            {date}
          </span>
          <span className="title">
            <img tooltip="csgo" src={icon} /> <span>{cheat.name}</span>
          </span>
          <div className="description">{cheat.description}</div>
          <div className="buttons">
            <Button
              onClick={downloadCheat}
              className="download_button"
              variant="outlined"
              to={"/cheat/" + cheat.slug}
              component={Link}
              startIcon={<FileDownload />}
            >
              {/* <i className="fas fa-download"></i> */}
              <span>Download</span>
            </Button>

            {!loadingUser ? (
              profile ? (
                profile.admin ? (
                  <div className="admin-options">
                    <span
                      className="iconOptions iconTrash"
                      onClick={deleteCheatFunction}
                    >
                      <i className="fas fa-trash-alt"></i>
                    </span>
                    <span
                      className="iconOptions iconEdit"
                      onClick={editCheatFunction}
                    >
                      <i className="fas fa-edit"></i>
                    </span>
                  </div>
                ) : null
              ) : null
            ) : null}
          </div>
        </div>
      </div>
      <Cheat_Modal open={openModal} setOpen={setOpenModal} type="edit" />
    </>
  );
};

export default Cheat_Box_Preview;
