import React from 'react';

import "./troubleshoot.css";

export default function TroubleShoot_Question({question}) {
  const click =(e)=>{
    e.currentTarget.classList.toggle('show_troubleshoot')
  }
  return (
    <div className="troubleshoot_question" onClick={click}>
      <span>{question.Title}</span>
      <p>{question.Description}</p>
    </div>
  );
}