import React, { useState, useEffect } from "react";
import "./cheats_details.css";
import { useCheats } from "hooks/useCheats";
import Cheat_Details_Box from "components/cheats/cheat_details_box/cheat_details_box";
import { useParams } from "react-router-dom";
import ClipLoader from "react-spinners/ClipLoader";
import { useHistory } from "react-router-dom";

export default function Cheats_Details() {
  const { currentCheat, getCurrentCheat, loadingCheats } = useCheats(false);
  let { slug } = useParams();

  useEffect(() => {
    getCurrentCheat(slug);
  }, [slug]);

  return (
    <>
      {loadingCheats ? (
        <ClipLoader
          color={"var(--color_principal)"}
          loading={true}
          css={"display: block;margin: 0 auto"}
          size={150}
        />
      ) : (
        <Cheat_Details_Box cheat={currentCheat} />
      )}
    </>
  );
}
