import React from 'react';

import "./troubleshoot.css";

import TroubleShoot_Question from './troubleshoot_question';
import { useCheats } from 'hooks/useCheats';

export default function TroubleShoot() {
  const {questions} = useCheats()

  return (
    <div className="troubleshoot">
      <span><i className="fas fa-hammer"></i>Troubleshoot</span>
      <div className="troubleshoot_questions">
        {
          
          questions.map((q,index) =>
          <TroubleShoot_Question
            key={index}
            question={q}
          />
        )
        }
        
      </div>
    </div>
  );
}